	import uniStatusBar from "../uni-status-bar/uni-status-bar.vue";
	import uniIcons from "../uni-icons/uni-icons.vue";

	export default {
		name: "UniNavBar",
		data(){
			return{
				statusBarHeight:''
			}
		},
		components: {
			uniStatusBar,
			uniIcons
		},
		props: {
			title: {
				type: String,
				default: ""
			},
			leftText: {
				type: String,
				default: ""
			},
			rightText: {
				type: String,
				default: ""
			},
			leftIcon: {
				type: String,
				default: ""
			},
			rightIcon: {
				type: String,
				default: ""
			},
			fixed: {
				type: [Boolean, String],
				default: false
			},
			color: {
				type: String,
				default: "#000000"
			},
			backgroundColor: {
				type: String,
				default: "#FFFFFF"
			},
			statusBar: {
				type: [Boolean, String],
				default: false
			},
			shadow: {
				type: [String, Boolean],
				default: false
			},
			border: {
				type: [String, Boolean],
				default: true
			},

		},
        mounted() {
          if(uni.report && this.title !== '') {
              uni.report('title', this.title)
          }
		  let self = this
		  uni.getSystemInfo({
		  	success: function(res) {
		  		let modelmes = res.model;
		  		if (modelmes.search('iPhone X') != -1) {
		  			// self.globalData.isIphoneX = true;
					self.statusBarHeight =res.statusBarHeight*.75
		  		} else if (modelmes.search('iPhone 5') != -1) {
		  			// self.globalData.isIphone5 = true;
					self.statusBarHeight = res.statusBarHeight *.8
		  
		  		}
		  		// console.log(res.model);
		  		// console.log(res.pixelRatio);
		  		// console.log(res.windowWidth);
		  		// console.log(res.windowHeight);
		  		// console.log(res.statusBarHeight)
		  		// console.log(res.navigationBarHeight)
		  		// console.log(res.language);
		  		// console.log(res.version);
		  		// console.log(res.platform);
		  	}
		  });
        },
		methods: {
			onClickLeft() {
				let pages = getCurrentPages();
				if(pages.length - 1 == 0){
					uni.switchTab({
						url:"../../pages/home/home"
					})
					return
				};
				uni.navigateBack()
				this.$emit("clickLeft");
			},
			onClickRight() {
				this.$emit("clickRight");
			}
		}
	};
